<template>
  <section class="hero is-fullheight">
    <div class="hero-body">
      <div class="container">
        <p>
          <span class="about">About/></span>
        </p>
        <p>
          <span class="function">function</span>
          <span class="prop">about</span>(name, dateofBirth, email) {
        </p>
        <p>
          <span class="dot">....</span><span class="scope">this</span>.<span class="prop2">name</span> = <span class="string">'Teerawut Kitbunjerdjarud'</span>
        </p>
        <p>
          <span class="dot">....</span><span class="scope">this</span>.<span class="prop2">dateofBirth</span> = <span class="string">new Date('November 8, 1996')</span>
        </p>
        <p>
          <span class="dot">....</span><span class="scope">this</span>.<span class="prop2">email</span> = <span class="string">'t.kitbunjerdjarud@gmail.com'</span>
        </p>
        <p>}</p>
        <p>
          <span class="function">function</span>
          <span class="prop">workExperience</span>() {
        </p>
        <p>
          <span class="dot">....</span><span class="function">return</span> [
        </p>
        <p>
          <span class="dot">........</span>{<span class="string"> 'Jun 2018 - Nov 2018' : 'Intern (Cooperative Education)'</span> }
        </p>
        <p>
          <span class="dot">....</span>]
        </p>
        <p>}</p>
        <p>
          <span class="function">function</span>
          <span class="prop">education</span>() {
        </p>
        <p>
          <span class="dot">....</span><span class="function">return</span> [
        </p>
        <p>
          <span class="dot">........</span>{<span class="string"> '2015 - 2019' : 'King Mongkut’s Institute of Technology Ladkrabang'</span> },
        </p>
        <p>
          <span class="dot">........</span>{<span class="string"> '2003 - 2015' : 'Assumption Samutprakarn School'</span> }
        </p>
        <p>
          <span class="dot">....</span>]
        </p>
        <p>}</p>
        <p>
          <span class="function">function</span>
          <span class="prop">skills</span>() {
        </p>
        <p>
          <span class="dot">....</span><span class="function">return</span> [
        </p>
        <p>
          <span class="dot">........</span><span class="string">'HTML/CSS/JS', 'Vue.js', 'jQuery', 'Bootstrap/Bulma', 'NPM', 'Python', 'Firebase', 'MySQL', 'MariaDB', 'Photoshop'</span>
          <span class="cursor"></span>
        </p>
        <p>
          <span class="dot">....</span>]
        </p>
        <p>}</p>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: "About"
};
</script>

<style scoped>
@import url("https://fonts.googleapis.com/css?family=PT+Mono");

* {
  background-color: #1a1527;
  color: white;
  font-family: "PT Mono", sans-serif;
  font-size: 1em;
}

::selection {
  color: #fff;
  background-color: #5918df;
}

.about {
  font-size: 2rem;
  color: #78707e;
}

.scope {
  color: #e3244c;
  display: inline;
}

.function {
  color: #5918df;
  display: inline;
}
.dot {
  color: #3b3553;
  display: inline;
}
.string {
  color: #24e33a;
}
.prop {
  color: #eeff31;
}

.prop2 {
  color: #24e394;
}
.cursor:before {
  opacity: 0;
  content: "|";
  -webkit-animation: blink 0.8s infinite;
  animation: blink 0.8s infinite;
  animation-duration: 0.8s;
  animation-timing-function: ease;
  animation-delay: 0s;
  animation-iteration-count: infinite;
  animation-direction: normal;
  animation-fill-mode: none;
  animation-play-state: running;
  animation-name: blink;
}
</style>


